@media only screen and (max-width: 500px) {
  .fancybox-close {
    top: 4%;
    right: 4%;
    background-size: 20px; }

  .feedback-form-modal {
    min-height: 420px;
    width: 100%;
    noindex {
      margin-top: 6.5vh;
      .fancybox-title {
        font-size: 38px; }

      span {
        margin-top: 0px;
        font-size: 0.3em; } }
    form {
      margin-top: 0px;
      input {
        height: 50px; }
      input[type='text'] {
        font-size: 14px; }
      input[type='submit'] {
        font-size: 16px;
        padding-left: 31px; }
      > div {
        input[type='checkbox'] {
          height: 17px; }
        input[type='checkbox'] + label {
          font-size: 15px; } } } } }
