.wrapper-container {
  margin: auto;
  max-width: 1920px;
  .wrapper-top-bar {
    width: 100%;
    height: 40px;
    display: flex;
    padding: 0px 35px 0px;
    justify-content: space-between;
    align-items: center;
    .we-work {
      font-size: 14px;
      color: white;
      span {
        font-family: 'Gotham Pro';
        font-weight: 800; } }
    .best-price-btn {
      position: relative;
      text-transform: uppercase;
      text-decoration: none;
      color:  $lyellow;
      font-family: 'Gotham Pro';
      font-weight: 800;
      border-bottom: 2px solid  $lyellow;
      text-decoration: none;
      padding-left: 30px;
      &::after {
        position: absolute;
        content: '';
        display: block;
        height: 20px;
        width: 20px;
        top: -3px;
        left: -1px;
        background-image: url('../images/ok_yellow.svg'); }
      //img
      //margin-right: 5px
 }      //margin-bottom: 3px

    .your-city {
      position: relative;
      color: white;
      font-size: 14px;
      font-family: 'Gotham Pro';
      font-weight: 800;
      display: flex;
      span {
        text-decoration: underline;
        color: white;
        font-family: 'Gotham Pro';
        font-weight: 300; }
      &::after {
        content: '';
        position: absolute;
        width: 0px;
        height: 0px;
        right: -10px;
        top: 5px;
        border-left: 3px solid transparent;
        border-right: 3px solid transparent;
        border-top: 5px solid $white; } } }
  @include media ('<small') {
    .wrapper-top-bar {
      padding: 0px; } } }

.nav {
  .header-nav-container {
    background: white;
    display: flex;
    height: 67px;
    justify-content: space-between;
    align-items: center;
    position: relative;
    padding: 0px 20px;
    .main-submenu {
      // margin: 0
      // list-style: none
      // white-space: nowrap
      // padding: 5px
      // z-index: 1
      // position: absolute
      // top: 100%
      // left: 0
      // background: grey
      // padding: 10px 20px
      // padding-top: 40px
      // //display: none
      // min-width: calc(100%)
      a {
        font-size: 14px;
        color: $black;
        text-decoration: none !important; } }
    .header-main-menu {
      font-family: 'Gotham Pro';
      font-weight: 400;
      span {
        z-index: 2; }
      .header-main-menu-list {
        margin-left: -40px;
        margin-bottom: 0px;
        margin-top: 0px;
        list-style: none;
        display: flex;
        .header-main-menu-list__item {
          text-transform: uppercase;
          color: $black;
          font-family: 'Gotham Pro';
          font-weight: 400; }
        .with-submenu {
          > span {
            padding-right: 15px;
