                    svg {
                      border-right: 1px solid lightgrey;
                      width: 100%;
                      height: 90px; }
                    span {
                      display: block;
                      max-width: 100%;
                      text-align: center;
                      word-spacing: 1337px;
                      white-space: normal;
                      line-height: 18px;
                      .invisible {
                        display: inline;
                        color: white; } } }
                  &:last-child {
                    a {
                      svg {
                        border-right: none; } } } } } } }
          .main-menu-sub-1 {
            li {
              list-style: none; }
            > li, ul {
              display: flex;
              flex-flow: row nowrap;
              justify-content: flex-start; }
            > li {
              > a {
                font-size: 20px;
                font-family: 'Gotham Pro';
                font-weight: 800;
                letter-spacing: -0.5px;
                display: block;
                width: 25%;
                margin-top: 4px; } }
            .main-submenu2 {
              border-top: 1px solid lightgrey;
              width: 82%;
              margin-top: 20px;
              padding-top: 15px;
              li {
                flex-basis: 19%; }
              a {
                display: flex;
                flex-flow: column nowrap;
                align-items: center;
                svg {
                  width: 100%;
                  height: 90px; }
                &:hover {
                  svg {
                    .st1, .st0, .st2, .st3 {
                      stroke: $hyellow; } } }
                span {
                  display: block;
                  max-width: 100%;
                  text-align: center;
                  word-spacing: 1337px;
                  white-space: normal;
                  line-height: 18px; } }
              .premium-menu-item {
                margin-top: -15px;
                margin-bottom: -20px;
                background-color: $black;
                svg {
                  position: relative;
                  top: 0px;
                  .st0 {
                    stroke: $lyellow; } }
                &:hover {
                  svg {
                    position: relative;
                    top: -3px; } }
                a {
                  margin-top: 15px;
                  color: $lyellow;
                  font-family: 'Gotham Pro';
                  font-weight: 800; } } } }

          .main-menu-sub-2, .main-menu-sub-3, .main-menu-sub-4 {
            .main-submenu2 {
              display: flex;
              flex-flow: row nowrap;
              justify-content: center;
              a {
                display: flex;
                flex-flow: column nowrap;
                align-items: center;
                svg {
                  width: 150px;
                  height: 150px; }
                &:hover {
                  svg {
                    .st1, .st0, .st2, .st3 {
                      stroke: $hyellow; } } } } } } }



        .header-main-menu-list__item + li {
          margin-left: 27px;
          a {
            color: $black;
            font-size: 14px;
            text-decoration: none;
            z-index: 2; } } }
      > a, span {
        font-size: 14px;
        font-weight: 400;
        line-height: 14px;
        position: relative;
        text-decoration: none;
        transition: .2s; } }
    .header-nav-phones {
      font-family: 'Gotham Pro';
      font-weight: 800;
      z-index: 2;
      .header-nav-phones__link {
        text-decoration: none;
        color: $black;
        text-decoration: none; } }
    .header-order-call-btn {
      font-family: 'Gotham Pro';
      font-weight: 800;
      color: $black;
      z-index: 2;
      padding: 4px 30px;
      span {
        display: block;
        margin-top: 3px; } }
    .header-basket {
      margin-right: 15px;
      margin-top: 5px;
      position: relative;
      z-index: 2;
      .basket-count {
        position: absolute;
        top: -11px;
        right: -2px;
        background-color:  $lyellow;
        color: $black;
        border-radius: 50%;
        font-size: 10px;
        font-family: 'Gotham Pro';
