/*---------------------------------- */
/* HEADER CART MENU */

.header-nav-container {
  .hide-sm {
    display: block; }
  .hide-lg {
    display: none; }
  #header-basket__hover {
    transition: 0.3s;
    position: absolute;
    right: 0px;
    top: 100%;
    background-color: white;
    display: flex;
    flex-flow: column nowrap;
    .basket__item {
      position: relative;
      width: 600px;
      height: 85px;
      margin: 15px 25px;
      display: flex;
      flex-flow: column wrap;
      img {
        width: 115px;
        display: block; }
      .basket__item-text {
        display: flex;
        flex-flow: column nowrap;
        justify-content: space-between;
        width: 70%;
        height: calc(100%);
        p {
          margin-top: 5px;
          display: block;
          text-transform: uppercase;
          font-family: 'Gotham Pro';
          font-weight: 800;
          &:nth-child(1) {
            padding-right: 20%;
            margin-bottom: 0px; }
          &:nth-child(2) {
            font-family: 'Gotham Pro';
            font-weight: 300;
            margin-bottom: 5px; } } }
      .basket__item-close {
        position: absolute;
        height: 15px;
        width: 15px;
        right: 10px;
        top: 10px;
        background: url('../images/rd_close_cross_yellow.svg') no-repeat;
        cursor: pointer; } }
    .basket__buttons {
      display: flex;
      flex-flow: row nowrap;
      margin: 0px;
      margin-top: 100px;
      width: 100%;
      a, p {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 60px;
        margin-bottom: 0px;
        width: 50%;
        text-decoration: none; }
      p {
        font-size: 30px;
        font-family: 'Gotham Pro';
        font-weight: 800;
        margin-right: auto;
        margin-top: 0px;
        .basket__item-price {
          display: block; } }
      .basket__keep-shopping-btn {
        text-align: center;
        cursor: pointer;
        margin-left: auto;
        color: $black; } } } }

.basket_empty_warning {
  width: 650px;
  font-size: 20px;
  color: $lightgrey;
  margin: 50px 0px 50px 0px;
  text-align: center; }



